.Header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1em 1.5em;
  margin: 0 0 .5em;
}

.Header-button {
  padding: .5em .6em;
  color: #0E9AA7;
  font-size: 1em;
  border: 1px solid #0E9AA7;
  border-radius: .3em;
  transition: 200ms;

  [class^='fa'] { // Font Awesome icons
    font-size: 80%;
    margin-right: .6em;
  }

  &:hover {
    background: #0E9AA7;
    color: white;
  }
}

.Header-pageTitle {
  margin: 0;
  font-size: 1.5rem;
  color: #666;
  font-weight: normal;
}



