.Form {

}

.Form-control {
  display: block;
  margin: 15px 0;
  position: relative;
}

.Form-error {
  position: absolute;
  right: 17px;
  top: -8px;
  background: white;
  padding: 0 3px;
  color: #ea6153;
  font-weight: bold;
  font-size: 0.7em;
  animation: shake 500ms;
}

@keyframes shake {
  0% {
    transform: translate(0, 0);
  }
  20% {
    transform: translate(2px, 0);
  }
  40% {
    transform: translate(2px, 0);
  }
  60% {
    transform: translate(-2px, 0);
  }
  80% {
    transform: translate(2px, 0);
  }
  100% {
    transform: translate(0, 0);
  }
}

.Form-field {
  background: white;
  appearance: none;
  width: 100%;
  padding: .7em 15px;
  font-size: .8em;
  border: 1px solid #ccc;
  border-radius: 5px;
  resize: vertical;

  &:focus {
    outline: none;
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.15);
  }
}

select.Form-field {
  position: relative;
  background: url('data:image/png;base64,')
              no-repeat right 15px center;
}

.Form-button {
  cursor: pointer;
  display: inline-block;
  padding: .5em 1em;
  font-size: 1em;
  font-weight: 300;
  border-radius: 3px;
  background: #0E9AA7;
  color: white;
  border: none;
  transition: background 200ms, text-shadow 200ms, box-shadow 200ms;

  & + & {
    margin: 0 0 0 .5em;
  }

  &:hover {
    background: lighten(#0E9AA7, 3%);
    text-shadow: 0 0 3px lighten(#0E9AA7, 30%);
    box-shadow: 0 0 5px rgba(0,0,0,0.25);
  }

  &:active {
    background: darken(#0E9AA7, 3%);
  }
}



